/* TypingTextCarousel.css */

.typing-text-carousel {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
  }
  
  .typing-text-carousel h1 {
    color: #fff;
    font-size: 60px;
    text-align: center;
    font-weight: bold;
  }
  